raul-progress {
  display: flex;
  flex-direction: column;
  font-size: 0.875rem;
  font-size: 0.875rem;
}

raul-progress .r-progress__label {
  display: flex;
  align-items: flex-end;
  font-size: 0.75rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

raul-progress .r-progress__remove-btn {
  margin-left: auto;
}

raul-progress .r-progress__bar {
  background-color: #EBEDEE;
  height: 0.25rem;
  border-radius: 9999px;
  overflow: hidden;
  position: relative;
}

raul-progress .r-progress__bar__value {
  background-color: #0076CC;
  border-radius: 9999px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

raul-progress .r-progress__text {
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

raul-progress[static] {
  flex-direction: row;
  align-items: center;
}

raul-progress[static] .r-progress__label {
  font-weight: 400;
  margin-bottom: 0;
  margin-right: 0.5rem;
}

raul-progress[static] .r-progress__bar {
  flex: 0 0 5rem;
}

raul-progress[static] .r-progress__text {
  margin-top: 0;
  margin-left: 0.5rem;
}

raul-progress[color=warning] .r-progress__bar__value {
  background-color: #FEC12D;
}

raul-progress[color=danger] .r-progress__bar__value {
  background-color: #D01A1F;
}

raul-progress[color=success] .r-progress__bar__value {
  background-color: #139C3E;
}